ce: nowrap;\n  cursor: pointer;\n  border: 0;\n  outline: none;\n  transition: box-shadow .2s $animation-curve-fast-out-linear-in,\n              background-color .2s $animation-curve-default,\n              color .2s $animation-curve-default;\n  &::-moz-focus-inner {\n    border: 0;\n  }\n  > span:not([data-react-toolbox=\"tooltip\"]) {\n    display: inline-block;\n    line-height: $button-height;\n    vertical-align: middle;\n  }\n  > svg {\n    display: inline-block;\n    width: 1em;\n    height: 1em;\n    font-size: 120%;\n    vertical-align: middle;\n    fill: currentColor;\n  }\n  > * {\n    pointer-events: none;\n  }\n  [data-react-toolbox=\"ripple\"] {\n    overflow: hidden;\n  }\n  &[disabled] {\n    color: $button-disabled-text-color;\n    pointer-events: none;\n    cursor: auto;\n  }\n}\n\n%squared {\n  min-width: $button-squared-min-width;\n  padding: $button-squared-padding;\n  border-radius: $button-border-radius;\n  .icon {\n    margin-right: $button-squared-icon-margin;\n    font-size: 120%;\n    vertical-align: middle;\n  }\n  > svg {\n    margin-right: .5 * $unit;\n  }\n}\n\n%solid {\n  &[disabled] {\n    @include shadow-2dp();\n    background-color: $button-disabled-background-color;\n  }\n  &:active {\n    @include shadow-4dp();\n  }\n  &:focus:not(:active) {\n    @include focus-shadow();\n  }\n}\n\n.raised {\n  @extend %button;\n  @extend %squared;\n  @extend %solid;\n  @include shadow-2dp();\n}\n\n.flat {\n  @extend %button;\n  @extend %squared;\n  background: transparent;\n}\n\n.floating {\n  @extend %button;\n  @extend %solid;\n  width: $button-floating-height;\n  height: $button-floating-height;\n  font-size: $button-floating-font-size;\n  border-radius: 50%;\n  box-shadow: 0 1px 1.5px 0 rgba(0, 0, 0, .12),\n              0 1px 1px 0 rgba(0, 0, 0, .24);\n  .icon {\n    line-height: $button-floating-height;\n  }\n  [data-react-toolbox=\"ripple\"] {\n    border-radius: 50%;\n  }\n  &.mini {\n    width: $button-floating-mini-height;\n    height: $button-floating-mini-height;\n    font-size: $button-floating-mini-font-size;\n    .icon {\n      line-height: $button-floating-mini-height;\n    }\n  }\n}\n\n.toggle {\n  @extend %button;\n  width: $button-height;\n  background: transparent;\n  border-radius: 50%;\n  > .icon, svg {\n    font-size: $button-toggle-font-size;\n    line-height: $button-height;\n    vertical-align: middle;\n  }\n  [data-react-toolbox=\"ripple\"] {\n    border-radius: 50%;\n  }\n}\n\n.neutral:not([disabled]) {\n  &.raised, &.floating {\n    color: $button-neutral-color-contrast;\n    background-color: $button-neutral-color;\n  }\n  &.flat, &.toggle {\n    color: $button-neutral-color-contrast;\n    &:focus:not(:active) {\n      background: $button-neutral-color-hover;\n    }\n  }\n  &.flat:hover {\n    background: $button-neutral-color-hover;\n  }\n\n  &.inverse {\n    &.raised, &.floating {\n      color: $button-neutral-color;\n      background-color: $button-neutral-color-contrast;\n    }\n    &.flat, &.toggle {\n      color: $button-neutral-color;\n      &:focus:not(:active) {\n        background: $button-neutral-color-hover;\n      }\n    }\n    &.flat:hover {\n      background: $button-neutral-color-hover;\n    }\n  }\n}\n\n@include btn-colors('primary', $button-primary-color-contrast, $button-primary-color, $button-primary-color-hover);\n@include btn-colors('accent', $button-accent-color-contrast, $button-accent-color, $button-accent-color-hover);\n","// scss-lint:disable VendorPrefix\n@mixin typo-preferred-font($use-preferred: true) {\n  @if $use-preferred {\n    font-family: $preferred-font;\n  }\n}\n\n@mixin typo-display-4($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 11.2rem;\n  font-weight: 300;\n  line-height: 1;\n  letter-spacing: -.04em;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-display-3($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 5.6rem;\n  font-weight: 400;\n  line-height: 1.35;\n  letter-spacing: -.02em;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-display-2($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 4.5rem;\n  font-weight: 400;\n  line-height: 4.8rem;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-display-1($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 3.4rem;\n  font-weight: 400;\n  line-height: 4rem;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-headline($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 2.4rem;\n  font-weight: 400;\n  line-height: 3.2rem;\n  -moz-osx-font-smoothing: grayscale;\n\n  @if $color-contrast {\n    opacity: .87;\n  }\n}\n\n@mixin typo-title($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 2rem;\n  font-weight: 500;\n  line-height: 1;\n  letter-spacing: .02em;\n\n  @if $color-contrast {\n    opacity: .87;\n  }\n}\n\n@mixin typo-subhead($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 1.6rem;\n  font-weight: 400;\n  line-height: 2.4rem;\n  letter-spacing: .04em;\n\n  @if $color-contrast {\n    opacity: .87;\n  }\n}\n\n@mixin typo-subhead-2($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 1.6rem;\n  font-weight: 400;\n  line-height: 2.8rem;\n  letter-spacing: .04em;\n\n  @if $color-contrast {\n    opacity: .87;\n  }\n}\n\n@mixin typo-body-2($color-contrast: false, $use-preferred: false) {\n  @include typo-preferred-font($use-preferred);\n  font-size: 1.4rem;\n  line-height: 2.4rem;\n  letter-spacing: 0;\n\n  @if $use-preferred {\n    font-weight: 500;\n  } @else {\n    font-weight: bold;\n  }\n\n  @if $co